import { UNCATEGORIZED_BUDGET_ID } from "../contexts/BudgetContext";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";

// Register the components with ChartJS
ChartJS.register(ArcElement, Tooltip, Legend);

// Function to calculate total expenses for each budget
export function calculateChartData(budget, getBudgetExpense) {
  // Get the labels and values for the chart
  // Add a label and value for the uncategorized budget as the last element
  const labels = [...budget.map((b) => b.name), "Uncategorized"];
  const values = [
    ...budget.map((b) => {
      // reduce the expenses of the budget to get the total amount
      const totalExpense = getBudgetExpense(b.id).reduce(
        (total, expense) => total + expense.amount,
        0
      );
      return totalExpense;
    }),
    // Get the total amount of the uncategorized budget expenses
    getBudgetExpense(UNCATEGORIZED_BUDGET_ID).reduce(
      (total, expense) => total + expense.amount,
      0
    ),
  ];

  // return the data object for the chart with styling
  const colors = [
    "rgba(255, 99, 132, 0.6)", // Red
    "rgba(54, 162, 235, 0.6)", // Blue
    "rgba(255, 206, 86, 0.6)", // Yellow
    "rgba(75, 192, 192, 0.6)", // Teal
    "rgba(153, 102, 255, 0.6)", // Purple
    "rgba(255, 159, 64, 0.6)", // Orange
    "rgba(199, 199, 199, 0.6)", // Light Gray
    "rgba(83, 102, 255, 0.6)", // Indigo
    "rgba(255, 99, 255, 0.6)", // Pink
    "rgba(99, 255, 132, 0.6)", // Light Green
  ];

  const borderColors = [
    "rgba(255, 99, 132, 1)", // Red
    "rgba(54, 162, 235, 1)", // Blue
    "rgba(255, 206, 86, 1)", // Yellow
    "rgba(75, 192, 192, 1)", // Teal
    "rgba(153, 102, 255, 1)", // Purple
    "rgba(255, 159, 64, 1)", // Orange
    "rgba(199, 199, 199, 1)", // Light Gray
    "rgba(83, 102, 255, 1)", // Indigo
    "rgba(255, 99, 255, 1)", // Pink
    "rgba(99, 255, 132, 1)", // Light Green
  ];

  // return the data object for the chart
  return {
    labels,
    datasets: [
      {
        label: "Expenses",
        data: values,
        backgroundColor: labels.map((label, index) =>
          label === "Uncategorized"
            ? "rgba(132, 138, 134, 0.6)"
            : colors[index % colors.length]
        ),
        borderColor: labels.map((label, index) =>
          label === "Uncategorized"
            ? "rgba(132, 138, 134, 1)"
            : borderColors[index % borderColors.length]
        ),
        borderWidth: 1,
        hoverBackgroundColor: labels.map((label, index) =>
          label === "Uncategorized"
            ? "rgba(132, 138, 134, 0.8)"
            : colors[index % colors.length].replace("0.6", "0.8")
        ),
        hoverBorderColor: labels.map((label, index) =>
          label === "Uncategorized"
            ? "rgba(132, 138, 134, 1)"
            : borderColors[index % borderColors.length]
        ),
        shadowOffsetX: 2,
        shadowOffsetY: 2,
        shadowBlur: 10,
        shadowColor: "rgba(0, 0, 0, 0.5)",
      },
    ],
  };
}
