import React from "react";
import BudgetCard from "./BudgetCard";
import { useBudgets } from "../contexts/BudgetContext";

// TotalBudgetCard to show the total budget and expenses
export default function TotalBudgetCard() {
  const { budget, expense } = useBudgets();

  // If there are no budgets, return null otherwise calculate the total amount
  const amount = expense.reduce((total, expense) => total + expense.amount, 0);

  // Calculate the total max budget
  const max = budget.reduce((total, budget) => total + budget.maxBudget, 0);

  // If the total max budget is 0, return null
  if (max === 0) return null;

  // Use a BudgetCard component to display the total budget and expenses
  return <BudgetCard name="Total" gray max={max} amount={amount} hideButtons />;
}
