import { Pie } from "react-chartjs-2";
import React, { useMemo } from "react";
import { useBudgets } from "../contexts/BudgetContext";
import { calculateChartData } from "./PieChartData";

// BudgetSummary component: this is the parent component that will render the PieChart component
export default function BudgetSummary() {
  const { budget, getBudgetExpense } = useBudgets();

  // Use memo to calculate the chart data only when the budget or getBudgetExpense changes
  const chartData = useMemo(
    () => calculateChartData(budget, getBudgetExpense),
    [budget, getBudgetExpense]
  );

  // PieChart component is
  return (
    <div style={{ margin: "50px 20px" }}>
      <h2 className="text-center text-secondary fw-bold text-uppercase fs-4 my-4">
        Budget Summary
      </h2>
      {/* Pass the dynamic chart data to the PieChart component */}
      <PieChart data={chartData} />
    </div>
  );
}

// PieChart used the data prop form the parent component to render the chart
const PieChart = ({ data }) => {
  return (
    <div
      style={{
        height: "100%",
        width: "100%",
        maxWidth: "500px",
        maxHeight: "500px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        margin: "0 auto",
      }}
    >
      {/* Render the Pie chart with the data */}
      <Pie data={data} />
    </div>
  );
};
